.pageTitle {
  /* border-bottom: 1px solid rgb(255, 255, 255); */
  -ms-flex-align: start;
      align-items: flex-start;
  display: block;
  padding: 10px;
  font-size: 26px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: black;
  background-color: rgb(18, 179, 31);
  margin-bottom: 20px;
}
.btnAction {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.basket-item{
  display: -ms-flexbox;
  display: flex;
}
.addition {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: 3px;
}
.addition p {
  color: white;
  font-weight: bold;
  border: solid #ededed60;
  padding: 3px 6px 3px 6px;
}
.btnAdd {
  margin-left: 5px;
  margin-right: 5px;
  
}
.btnMin {
  margin-left: 5px;
  margin-right: 5px;
}
.btnAction .btnRemove {
  width: 70px;
  height: 35px;
  background-color: rgb(184, 30, 19);
  color: wheat;
}
.subTotal {
  color: white;
  background-color: #080e2c;
  margin-bottom: 100px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
}
.basket-item{
  display: block;
}



