.header {
  width: 100%;
  cursor: pointer;
  /* position: static; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  text-transform: uppercase;
  background-color: #080e2c;
  font-family: "Montserrat", sans-serif;
  font-size: 2vw;
  /* padding: 15px; */
  padding: 1rem 0rem;
  -webkit-box-shadow: 0px 1px 5px black;
          box-shadow: 0px 1px 5px black;
  color: white;
  z-index: 100;
  margin-bottom: 15px;
}

@media (max-width: 1000px) {
  .header {
    padding-top: 15px;
    font-size: 6.4vw;
  }
}



